import { Block, Plugin } from '@ali/4ever-cangjie';
import { clearCalloutSelected } from '../actions';

// 只要仓颉触发任何光标选择操作，就清除高亮块全选态
// 同时有仓颉光标选区或光标 与 高亮块整体选中态 是冲突的
// 两个状态同时存在时，按下 delete 等键会有歧义
function createOnCangjieSelect(): Plugin['onCangjieSelect'] {

  return function onCangjieSelect(event, controller, next) {
    if (controller.value.data.selectedCallout) {
      controller.run('onAction', clearCalloutSelected());
    }

    return next();
  };
}

export default createOnCangjieSelect;
